body {
  background-color: #F2F2F2;
  font-family: "Lato", sans-serif;
  font-size: 15px; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif; }

img {
  max-width: 100%; }

a {
  transition: all 0.500s ease; }

blockquote {
  position: relative;
  margin-left: 0;
  margin-right: 0;
  padding-left: 1.5em;
  padding-right: 1.5em; }
  blockquote p {
    font-size: 1.3em; }
  blockquote::before {
    color: #6AABEE;
    content: "\f10d";
    font-family: "FontAwesome";
    position: absolute;
    left: 5px;
    top: 0; }
  blockquote::after {
    color: #6AABEE;
    content: "\f10e";
    font-family: "FontAwesome";
    position: absolute;
    right: 5px;
    bottom: 0; }

.container {
  background-color: #fff; }

.box-servicios-actualidad,
.box-fotos-videos,
.box-documentos-interes,
.box-lideres,
.box-enlaces-interes,
.box-footer {
  padding-top: 1.5em;
  padding-bottom: 1.5em; }

.box-footer {
  padding-bottom: 0; }

.box-logos {
  margin-top: 0.5em; }
  .box-logos > img {
    max-height: 60px; }
    @media (max-width: 1199px) {
      .box-logos > img {
        max-height: 40px; } }

.box-redes-sociales {
  margin-top: 0.5em; }

@media (max-width: 991px) {
  .box-logos, .box-redes-sociales {
    border-top: 3px solid #36C171;
    margin-top: 0;
    padding-top: 0.5em;
    padding-bottom: 0.5em; } }

.img-meta-veinte img {
  margin-left: 10px;
  margin-top: 5px; }
  @media (max-width: 1199px) {
    .img-meta-veinte img {
      margin-top: 0;
      max-height: 40px; } }
  @media (max-width: 767px) {
    .img-meta-veinte img {
      margin-left: 0; } }

.redes-sociales {
  margin-bottom: 0.5em; }
  @media (max-width: 991px) {
    .redes-sociales {
      margin-bottom: 0;
      padding-top: 0.5em; } }
  .redes-sociales li {
    box-sizing: border-box; }
    @media (max-width: 991px) {
      .redes-sociales li:nth-child(1), .redes-sociales li:nth-child(2), .redes-sociales li:nth-child(3) {
        display: none; } }
    .redes-sociales li a {
      color: #009933;
      text-decoration: none; }
    .redes-sociales li .fa {
      background-color: #009933;
      border-radius: 50%;
      border: thin solid transparent;
      color: #fff;
