      transition: all 0.500s ease;
      height: 30px;
      width: 30px;
      padding-top: 5px; }
      .redes-sociales li .fa.fa-facebook:hover {
        background-color: #fff;
        border: thin solid #336699;
        color: #336699; }
      .redes-sociales li .fa.fa-twitter:hover {
        background-color: #fff;
        border: thin solid #0099CC;
        color: #0099CC; }
      .redes-sociales li .fa.fa-youtube:hover {
        background-color: #fff;
        border: thin solid #CC3333;
        color: #CC3333; }
    .redes-sociales li .fa-facebook {
      background-color: #336699; }
    .redes-sociales li .fa-twitter {
      background-color: #0099CC; }
    .redes-sociales li .fa-youtube {
      background-color: #CC3333; }

.box-buscador {
  padding-bottom: 0.5em; }
  @media (max-width: 991px) {
    .box-buscador {
      order: 3;
      padding-top: 0.5em;
      padding-bottom: 1em; } }
  .box-buscador .buscador-form {
    margin-top: -10px;
    position: relative; }
    @media (max-width: 1199px) {
      .box-buscador .buscador-form {
        margin-top: 0; } }
    .box-buscador .buscador-form .js_icono_buscador {
      color: #B0AEAE;
      position: absolute;
      right: 5px;
      top: 12px; }
    .box-buscador .buscador-form .form-control {
