.logo-mobile.d-flex {
  display: none !important; }
  @media (max-width: 991px) {
    .logo-mobile.d-flex {
      display: flex !important; } }
  @media (max-width: 991px) {
    .logo-mobile.d-flex .img-logo {
      max-height: 41px; } }
  .logo-mobile.d-flex .texto-drea {
    font-size: 13px;
    line-height: 16px;
    margin-bottom: 0; }

.navbar {
  background-color: #36C171;
  margin-bottom: 1em; }
  @media (max-width: 991px) {
    .navbar {
      min-height: 40px;
      margin-bottom: 0.5em; } }

.titulo {
  text-align: center;
  margin-top: 0;
  margin-bottom: 1em;
  position: relative;
  width: 100%; }
  .titulo::before {
    background-color: #36C171;
    content: "";
    margin-left: -10%;
    height: 5px;
    width: 20%;
    position: absolute;
    bottom: -5px;
    left: 50%;
    z-index: 100; }

@media (max-width: 991px) {
  .slider-pro h2.sp-layer {
    font-size: 1.2em; } }

@media (max-width: 767px) {
  .slider-pro h2.sp-layer {
    font-size: 1.1em; } }

@media (max-width: 575px) {
  .slider-pro h2.sp-layer {
    font-size: 1em; } }

@media (max-width: 991px) {
  .servicios {
    margin-bottom: 2em; } }

.lista-servicios .servicios-item {
