  .lista-servicios .servicios-item:hover {
    border: thin dotted #18814B; }

.lista-servicios .servicios-link {
  text-decoration: none !important; }
  .lista-servicios .servicios-link:visited {
    text-decoration: none; }
  .lista-servicios .servicios-link .fa {
    color: #666666;
    font-size: 2.5em;
    width: 40px;
    margin-right: 5px; }

.lista-servicios .servicios-contenido {
  width: 77%; }
  .lista-servicios .servicios-contenido small {
    color: #272525; }

.lista-servicios .servicios-titulo {
  color: #18814B;
  font-size: 1.1em; }

.actualidad-cabecera {
  position: relative; }

.actualidad-ver-todos {
