.actualidad {
  margin-bottom: 1.5em; }
  .actualidad:last-child {
    margin-bottom: 0; }
  .actualidad .image {
    background-size: cover;
    background-position: center center;
    height: 145px;
    width: 30%; }
  .actualidad .actualidad-contenido {
