    .actualidad .actualidad-meta-leer-mas:hover {
      border: thin solid #0A872F;
      color: #3D3939; }
    .actualidad .actualidad-meta-leer-mas:visited {
      color: #fff; }
  .actualidad .actualidad-meta-parrafo {
